import React, { useState, useEffect, SyntheticEvent } from 'react'
import {Sort,RColor,RNumber,MinMax} from '../services/Utils'
import Button from '../components/Button'
import Text from '../components/Text'
import View from '../components/View'
import List from '../components/List'
import './../App.scss'

function App() {
	const [player, setplayer] = useState<string>('')
	const [steps, setSteps] = useState<number[]>([2, 16])
	const [currentStep, setcurrentStep] = useState<number>(0)
	const [isGameOver, setisGameOver] = useState<boolean>(false)
	const [isHallOfFame, setisHallOfFame] = useState<boolean>(false)
	const [rankingList, setrankingList] = useState<{ value: number, text: string }[]>([])

	useEffect(() => {
		setSteps(steps => MinMax(steps))
	}, [])

	const handleTileClick = (e: SyntheticEvent<HTMLDivElement>) => {
		const eTarget = e.target as Element
		if (eTarget.classList.contains('selected')) {
			if (steps.length === currentStep + 1) {
				setcurrentStep(currentStep + 1)
				setisGameOver(true)
			} else {
				setcurrentStep(currentStep + 1)
			}
		} else {
			setisGameOver(true)
		}
	}
	const handleTiles = (step: number) => {
		const squaresArr = []
		const color = RColor()
		const board = step * step
		const randomNumber = RNumber(board)
		for (let i = 0; i < board; i++) {
			squaresArr.push(
				<div
					className={'tile' + (i === randomNumber ? ' selected' : '')}
					style={{ background: color }}
					onClick={(e) => handleTileClick(e)}
					key={i}
				>
				</div>
			)
		}
		return squaresArr
	}
	const goToHallOfFame = (e: { preventDefault: () => void }) => {
		e.preventDefault()
		setisHallOfFame(true)
		rankingList.push({ 'value': currentStep, 'text': player })
		Sort(rankingList)
		setrankingList([...rankingList])
	}
	const playAgain = () => {
		setplayer('')
		setcurrentStep(0)
		setisGameOver(false)
		setisHallOfFame(false)
	}
/* 	const winCheat = () => {setcurrentStep(steps.length);setisGameOver(true)} */
	return (
		<>
		<div className='container section-first'>
			<View>
				<img className='circle self-center' src='ghavatar.png' alt='Bruno Galati Software Developer'/>
				<h1 className='lg'>Hi, I'm Bruno.</h1>
				<p className='text'>I'm specialized</p>
				<p className='text'>in software development</p>
				
			</View>
		{!isGameOver ?
			<View>
				<h1 className='over-square'>{currentStep}</h1>
				<div className={`board size-${steps[currentStep]}`}>
					{handleTiles(steps[currentStep])}
				</div>
{/* 				<Button onClick={() => winCheat()}>Win the game</Button> */}
			</View> :
			<>
			{!isHallOfFame ?
				<View>
					<h1>{currentStep === steps.length ? `Winner! üëë` : `Game Over! üíÄ`}</h1>
					<form>
						<Text
							autoFocus={true}
							maxLenght={15}
							required={true}
							value={player}
							placeholder='Insert your name'
							onChange={e => setplayer(e.target.value)}
						/>
						{player ? <Button onClick={(e) => goToHallOfFame(e)}>Next</Button> : null}
					</form>
				</View> :
				<View>
					<h1>Hall of fame üèÜ</h1>
					<List list={rankingList} limit={10} />
					<Button onClick={() => playAgain()} autoFocus={true}>Try again</Button>
				</View>
			}
			</>
		}
		</div>
		<div className='container section-block'>
			<img className='circle bg self-center' src='liavatar.png' alt='Bruno Galati Software Developer'/>
			<h1 className='lg'>About me</h1>
			<p className='text'>I'm a formed digital designer</p>
			<p className='text'>Building software and interfaces since 2008 and working in different countries</p>
		 	<div className='icons'>
				 <span>üáßüá∑</span>
				 <span>üáÆüáπ</span>
				 <span>üá©üá™</span> 
			</div>
		</div>
		<div className='container section-block-middle'>
			<h1 className='lg'>Services</h1>
			<em className='text'>Apps and Websites made to caught attention</em>
			<em className='text'>scalable and maintainable code</em>
			<em className='text'>Keep it simple</em>
			<div className='icons'>
				<i className='icon-javascript' title='javascript'></i>
				<i className='icon-react' title='React'></i>
				<i className='icon-angular' title='Angular'></i>
				<i className='icon-vue-dot-js' title='Vue'></i>
				<i className='icon-php' title='PHP'></i>
				<i className='icon-html-five' title='HTML5'></i>
				<i className='icon-css3' title='CSS3'></i>
			</div>
		</div>
		<div className='container section-block-deep'>
			<h1 className='lg'>Contact</h1>
			<p className='text'>Drop me a line, let's chat</p>
			<div className='icons'>
				<a href='https://www.linkedin.com/in/brunogalatideveloper/' target='_blank' rel="noreferrer"><i className='icon-linkedin2'></i></a>
				<a href='https://github.com/brunobruno' target='_blank' rel="noreferrer"><i className='icon-github'></i></a>
				<a href='mailto:brngalati@gmail.com'><i className='icon-mail4'></i></a>
				<a href="tel:+393892849607"><i className='icon-whatsapp'></i></a>
			</div>
		</div>
		</>
	)
}
export default App
